import Sentiment from 'sentiment';

export function getMoodFromText(text: string): 'joyful' | 'reflective' | 'sad' {
    const sentiment = new Sentiment();
    const result = sentiment.analyze(text);

    if (result.score > 3) return 'joyful';
    if (result.score < -2) return 'sad';
    return 'reflective';
} 